import { Button, TextField } from "@mui/material";
import styled from "@emotion/styled";
import axios from "axios";
import { Circles } from "react-loader-spinner";
import { useState } from "react";

axios.defaults.baseURL = "https://sport-school-server.onrender.com/api/";

const sendDate = async (formData) => {
  try {
    const date = await axios.post("news", formData, {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    });
    console.log(date);
  } catch (err) {
    console.log(err);
  }
};

const StyledForm = styled.form`
  & .block {
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: flex-start;
  }
  display: flex;
  gap: 30px;
`;
const Fallback = styled.div`
  width: 100vw;
  height: 100vh;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  background-color: #80808081;
`;
function App() {
  const [loading, setLoading] = useState(false);
  const onSubmit = async (event) => {
    setLoading(true);
    event.preventDefault();
    const formData = new FormData(event.target);
    formData.set("date", event.target.date.valueAsNumber);
    await sendDate(formData);
    setLoading(false);
  };
  return (
    <div className="App">
      {loading && (
        <Fallback className="fallback">
          <Circles />
        </Fallback>
      )}
      <StyledForm id="mainForm" onSubmit={onSubmit}>
        <div className="block">
          <TextField name="title" variant="outlined" label="" />
          <TextField
            name="textContent"
            sx={{
              width: "700px",
            }}
            minRows={20}
            maxRows={20}
            className="textBox"
            variant="outlined"
            label=""
            multiline
          />
        </div>
        <div className="block">
          <input form="mainForm" type="file" name="galery" multiple />
          <input form="mainForm" name="date" type="date" />
          <Button type="submit" variant="contained">
            
          </Button>
        </div>
      </StyledForm>
    </div>
  );
}

export default App;
